var first = require('first-match');

var nub = require('nub');

var data = require('./data');

var code = function code(_code) {
  _code = _code.toUpperCase();
  return first(data, function (c) {
    return c.code === _code;
  });
};

var country = function country(_country) {
  _country = _country.toLowerCase();
  return data.filter(function (c) {
    return (c.countries.map(function (c) {
      return c.toLowerCase();
    }) || []).indexOf(_country) > -1;
  });
};

var number = function number(_number) {
  return first(data, function (c) {
    return c.number === String(_number);
  });
};

var codes = function codes() {
  return data.map(function (c) {
    return c.code;
  });
};

var numbers = function numbers() {
  var items = data.map(function (c) {
    return c.number;
  }); // handle cases where number is undefined (e.g. XFU and XBT)

  return items.filter(function (n) {
    if (n) {
      return n;
    }
  });
};

var countries = function countries() {
  var m = data.filter(function (c) {
    return c.countries;
  }).map(function (c) {
    return c.countries;
  });
  return nub(Array.prototype.concat.apply([], m));
};

exports.code = code;
exports.country = country;
exports.number = number;
exports.codes = codes;
exports.numbers = numbers;
exports.countries = countries;